function Sliders() {
    let SlidersArray = []

    let sliders = document.querySelectorAll("._slider")
    sliders.forEach((slider,index) => {
        slider.setAttribute("data-slider-id",index)

        SlidersArray[index] = new Swiper(`._slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            watchSlidesProgress: true,
            preventClicks :true,
            a11y: false,
            observer: true,
            observeParents: true,
            observeSlideChildren: true,
            navigation: {
                nextEl: `._slider[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `._slider[data-slider-id="${index}"] .swiper-button-prev`,
            },
            pagination: {
                el: `._slider[data-slider-id="${index}"] .swiper-pagination`,
                type: 'bullets',
                clickable: true,
            },
            on: {
                init: function () {
                    renderCustomPagination(this)
                },
            },
        })

        SlidersArray[index].on('slideChange', function () {
            renderCustomPagination(SlidersArray[index])
        });

        function renderCustomPagination(swiper) {
            let slidesCount = document.querySelectorAll(`._slider[data-slider-id="${index}"] .swiper-slide:not(.swiper-slide-duplicate)`)
            let paginationCurrent = document.querySelector(`._slider[data-slider-id="${index}"] .custom-pagination ._current`)
            let paginationTotal = document.querySelector(`._slider[data-slider-id="${index}"] .custom-pagination ._total`)
            if(!paginationCurrent){
                return
            }
            paginationCurrent.innerHTML = formatPaginationNum(swiper.realIndex+1)
            paginationTotal.innerHTML = formatPaginationNum(slidesCount.length)
        }

        function formatPaginationNum(num) {
            if(num < 10){
                return `0${num}`
            }
            return num
        }
    })
}

Sliders()
