$(".product-card2__img svg").each(function (i,el) {
    let mask = $(el).find("._mask")
    let mask2 = $(el).find("._mask2")
    let imgFill = $(el).find("._img-fill")
    let pattern = $(el).find("._pattern")
    let img = $(el).find("._img")
    let use = $(el).find("._use")
    let rect = $(el).find("._rect")
    let pathMask = $(el).find("._path-mask")

    let maskContent = `mask${i}`
    let maskContent2 = `mask2${i}`
    let patternContent = `pattern${i}`
    let imgContent = `img${i}`

    $(mask).attr("id",`${maskContent}`)
    $(mask2).attr("id",`${maskContent2}`)
    $(imgFill).attr("mask",`url(#${maskContent})`)
    $(pattern).attr("id",`${patternContent}`)
    $(img).attr("id",`${imgContent}`)
    $(use).attr("xlink:href",`#${imgContent}`)
    $(rect).attr("fill",`url(#${patternContent})`)
    $(pathMask).attr("mask",`url(#${maskContent2})`)
})
