$(".gallery").each(function (i,el) {
    let itemsCount = $(el).find(".gallery__item:not(._more)").length
    $(el).find(".gallery__item-num").html(`+${itemsCount}`)
})

$("._not-auth-input").focus(function () {
    $(this).parents(".new-comment").find(".new-comment__auth").slideDown()
})

$(".comment-card__reply").click(function () {
    $(this).parents(".comment-card__bottom").siblings(".comment-card__answer").slideToggle()
})
