setActiveSelectItem()

$(".fg-select input").click(function () {
    $(this).parents(".fg-select").addClass("_open")
})

$(document).mouseup( function(e){
    var div = $( ".fg-select" );
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) {
        if($(div).hasClass("_open")){
            $(div).removeClass("_open")
        }
    }
});

$(".fg-dropdown__item").click(function () {
    $(this).siblings(".fg-dropdown__item").removeClass("_selected")
    $(this).addClass("_selected")
    $(this).parents(".fg-select").removeClass("_open")
    setActiveSelectItem()
})

function setActiveSelectItem() {
    $(".fg-select").each(function (i,el) {
        let field = $(el).find("input")
        let activeOption = $(el).find(".fg-dropdown__item._selected")
        console.log(field.val())
        $(field).val($(activeOption).attr("data-value"))
    })
}

