let burger = document.querySelector(".burger")
let body = document.querySelector("body")
let header = document.querySelector(".header")


if (burger) {
    burger.onclick = function() {
        header.classList.toggle("_open")
    }

    document.addEventListener( 'click', (e) => {
        const withinBoundaries = e.composedPath().includes(header);

        if ( ! withinBoundaries ) {
            closeMenu()
        }
    })
}

function closeMenu(){
    header.classList.remove("_open")
}

$(".main-slider").click(function () {
    $(".header-search").removeClass("_open")
    $(".header-search").removeClass("_type2")
})

$("._search-filed").focus(function () {
    $(".header-search").addClass("_open")
})

$("._search-filed").blur(function () {
    $(".header-search").removeClass("_open")
})

$("._search-filed").on("input",function () {
    if($(this).val().length > 0){
        $(".header-search").addClass("_type2")
    }
})

$(document).mouseup( function(e){
    var div = $( ".header-search" );
    if ( !div.is(e.target)
        && div.has(e.target).length === 0 ) {
        if($(div).hasClass("_type2")){
            $(div).removeClass("_open")
            $(div).removeClass("_type2")
        }
    }
});
